@charset "UTF-8";
:root {
  --container-width: 1330px;
  --container-padding: 40px;
  --theme-color-accent: #51a178;
  --theme-color-text-common: #223941;
  --font-family: "Didona", sans-serif;
  --second-family: "SFUIDisplay", sans-serif;
}

/*
Псевдоэлемент-расширитель для кнопок:
1. Помогает попадать по небольшим кнопкам
2. Помогает отлавливать событие нажатия на кнопку при наличии в ней различных элементов
*/
@font-face {
  font-family: Didona;
  src: url("../fonts/Didona-Normal.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: Didona;
  src: url("../fonts/Didona-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: Didona;
  src: url("../fonts/Didona-ExtraBold.woff2") format("woff2");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: SFUIDisplay;
  src: url("../fonts/SFUIDisplay-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: SFUIDisplay;
  src: url("../fonts/SFUIDisplay-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: SFUIDisplay;
  src: url("../fonts/SFUIDisplay-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

input,
button,
textarea {
  padding: 0;
  background: none;
  border: none;
  outline: none;
  color: inherit;
}

html,
body {
  height: 100%;
  padding: 0;
  scroll-behavior: smooth;
}

body {
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

ul,
ol {
  list-style-type: none;
  padding: 0;
}

a {
  text-decoration: none;
}

.main-wrapper-outer {
  width: 100%;
  min-height: 100%;
  max-width: var(--main-outer-width);
  margin-left: auto;
  margin-right: auto;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  overflow: hidden;
}

.main {
  flex-grow: 1;
  flex-shrink: 1;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: var(--container-width);
  padding: 0px 20px;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: transparent;
  z-index: 100;
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
}
@media (max-width: 768px) {
  .header {
    transition: background-color 0.3s ease, box-shadow 0s ease;
    background-color: #f8fafb;
    background-image: -webkit-linear-gradient(top, #cfded6 0%, rgba(207, 222, 214, 0) 14.9%, rgba(207, 222, 214, 0) 100%);
    background-image: linear-gradient(to bottom, #cfded6 0%, rgba(207, 222, 214, 0) 14.9%, rgba(207, 222, 214, 0) 100%);
  }
}
.header._header-scroll {
  background-color: #f8fafb;
  box-shadow: 0 35px 60px 0 rgba(8, 30, 22, 0.15);
}
@media (min-width: 992px) {
  .header._header-scroll .header__logo img {
    height: 100px;
  }
  .header._header-scroll .header__bottom {
    min-height: 60px;
  }
}
.menu-open .header._header-scroll {
  background-color: transparent;
  box-shadow: none;
}
.header__content {
  padding: 20px 0px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
@media (max-width: 992px) {
  .header__content {
    gap: 0px;
  }
}
@media (max-width: 768px) {
  .header__content {
    padding: 10px 0px;
  }
}
.header__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}
.header__top .contacts {
  flex: 0 0 27%;
}
@media (max-width: 992px) {
  .header__top .contacts {
    flex: 1 1 auto;
  }
}
@media (max-width: 480px) {
  .header__top .contacts {
    max-width: 230px;
  }
}
@media (max-width: 992px) {
  .header__top {
    align-items: start;
  }
}
.header__icon {
  display: none;
}
@media (max-width: 992px) {
  .header__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    flex: 0 0 40px;
    margin: 20px 0px 0px 0px;
  }
}
.header__logo {
  display: flex;
}
.header__logo img {
  height: 130px;
  width: auto;
  transition: height 0.3s;
}
@media (max-width: 992px) {
  .header__logo img {
    height: 100px;
  }
}
.header__phone {
  display: none;
}
@media (max-width: 992px) {
  .header__phone {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px 0px 0px 0px;
    flex: 0 0 40px;
    height: 40px;
    width: 40px;
    transition: opacity 0.3s;
  }
  .header__phone img {
    width: 36px;
    height: 36px;
  }
  .header__phone:hover {
    opacity: 0.7;
  }
}
.header__bottom {
  box-shadow: 0 30px 80px 0 rgba(43, 42, 41, 0.05);
  background: #fff;
  border-radius: 40px;
  display: flex;
  min-height: 80px;
}
@media (max-width: 992px) {
  .header__bottom {
    min-height: 0px;
    box-shadow: none;
    border-radius: 0px;
    display: block;
    background-color: transparent;
  }
}
.header__body {
  flex: 1 1 70%;
  display: flex;
  width: 100%;
}
@media (max-width: 992px) {
  .header__body {
    flex: 1 1 auto;
    flex-direction: column;
    align-items: center;
    position: absolute;
    z-index: 110;
    top: 100%;
    right: -100%;
    width: 100%;
    height: calc(100vh - 100%);
    gap: 22px;
    padding: 20px;
    transition: right 0.3s;
    overflow: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;
  }
  .header__body::-webkit-scrollbar {
    display: none;
  }
  .menu-open .header__body {
    right: 0;
  }
}
@media (max-width: 768px) {
  .header__body {
    padding: 40px 20px;
  }
}
.header__body ul {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}
@media (max-width: 992px) {
  .header__body ul {
    grid-template-columns: 1fr;
    gap: 30px;
    margin: 30px 0px 0px 0px;
  }
}
.header__body ul li {
  position: relative;
  display: flex;
  justify-self: center;
  align-items: center;
  font-weight: 400;
  font-size: 24px;
  line-height: 125%;
  width: 100%;
  text-align: center;
}
.header__body ul li a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 0px 5px;
  transition: color 0.3s;
}
.header__body ul li a:hover {
  color: var(--theme-color-accent);
}
.header__body ul li::after {
  content: "";
  position: absolute;
  right: 0;
  top: 20px;
  width: 1px;
  height: calc(100% - 40px);
  background-color: rgba(34, 57, 65, 0.1);
}
.header__body ul li:last-child::after {
  content: "";
  display: none;
}
@media (max-width: 992px) {
  .header__body ul li {
    font-size: 30px;
  }
}
.header__item {
  cursor: pointer;
  position: relative;
  z-index: 12;
  width: 100%;
  height: 100%;
  gap: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 40px 0px 0px 40px;
  background-color: #51a178;
  font-weight: 400;
  font-size: 24px;
  line-height: 125%;
  color: #fff;
  text-align: center;
  padding: 5px;
  transition: background-color 0.3s;
}
.header__item:hover {
  background-color: #ff6137;
}
.services-open .header__item {
  background-color: #ff6137;
}
.header__item .icon-menu {
  width: 18px;
  height: 15px;
}
.header__item .icon-menu::before, .header__item .icon-menu::after,
.header__item .icon-menu span {
  content: "";
  height: 2px;
  background-color: #fff;
}
.header__item .icon-menu::before {
  width: 12px;
}
.header__item .icon-menu::after {
  content: "";
  width: 12px;
}
.services-open .header__item .icon-menu::before {
  width: 100%;
  top: calc(50% - 1px);
  transform: rotate(-45deg);
}
.services-open .header__item .icon-menu span {
  width: 0;
}
.services-open .header__item .icon-menu::after {
  width: 100%;
  bottom: calc(50% - 1px);
  transform: rotate(45deg);
}
@media (max-width: 992px) {
  .header__item .icon-menu {
    display: none;
  }
}
@media (max-width: 992px) {
  .header__item {
    border-radius: 40px;
    flex: 1 1 auto;
    min-height: 60px;
    padding: 5px 10px;
  }
  .menu-open .header__item {
    display: none;
  }
  .services-open .header__item {
    border-radius: 20px;
    border-bottom: 1px dashed rgba(34, 57, 65, 0.1);
    background-color: #fff;
    color: var(--theme-color-text-common);
  }
}
.header__arrow {
  width: 16px;
  height: 10px;
  display: none;
  transition: transform 0.3s;
}
@media (max-width: 992px) {
  .header__arrow {
    display: block;
  }
  .services-open .header__arrow {
    transform: rotate(-180deg);
    filter: invert(56%) sepia(13%) saturate(1346%) hue-rotate(97deg) brightness(97%) contrast(86%);
  }
}
.header__services {
  flex: 0 1 21%;
  position: relative;
}
@media (min-width: 992px) {
  .header__services:hover .dropdowm-header {
    opacity: 1;
    pointer-events: auto;
  }
  .header__services:hover .header__item {
    background-color: #ff6137;
  }
  .header__services:hover .header__item .icon-menu::before {
    width: 100%;
    top: calc(50% - 1px);
    transform: rotate(-45deg);
  }
  .header__services:hover .header__item .icon-menu span {
    width: 0;
  }
  .header__services:hover .header__item .icon-menu::after {
    width: 100%;
    bottom: calc(50% - 1px);
    transform: rotate(45deg);
  }
}
@media (max-width: 992px) {
  .header__services {
    flex: 1 1 auto;
    margin: 20px 0px 0px 0px;
  }
}

.icon-menu {
  display: block;
  position: relative;
  width: 38px;
  height: 30px;
  cursor: pointer;
  z-index: 5;
}
.icon-menu span, .icon-menu::before, .icon-menu::after {
  content: "";
  transition: background-color 0.3s ease 0s, width 0.3s;
  right: 0;
  position: absolute;
  width: 100%;
  height: 4px;
  border-radius: 4px;
  background-color: var(--theme-color-text-common);
}
.icon-menu::before {
  top: 0;
  left: 0;
  width: 25px;
}
.icon-menu::after {
  width: 25px;
  right: 0;
  bottom: 0;
}
.icon-menu span {
  top: calc(50% - 1px);
}
.menu-open .icon-menu::before {
  width: 100%;
  top: calc(50% - 2px);
  transform: rotate(-45deg);
  background-color: var(--theme-color-accent);
}
.menu-open .icon-menu span {
  width: 0;
}
.menu-open .icon-menu::after {
  content: "";
  background-color: var(--theme-color-accent);
  width: 100%;
  bottom: calc(50% - 2px);
  transform: rotate(45deg);
}

.contacts {
  display: flex;
  align-items: start;
  gap: 15px;
}
.contacts.last {
  justify-content: flex-end;
}
@media (max-width: 480px) {
  .contacts.last {
    justify-content: start;
  }
}
.contacts__icon {
  flex: 0 0 40px;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 30px 80px 0 rgba(43, 42, 41, 0.05);
  background: #fff;
  border-radius: 50%;
  margin: -8px 0px 0px 0px;
}
.contacts__icon img {
  width: 24px;
  height: 24px;
}
.contacts__title {
  display: flex;
  flex-direction: column;
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: #223941;
}
.contacts__title span {
  font-weight: 600;
}
@media (max-width: 480px) {
  .contacts__text {
    display: flex;
    flex-direction: column;
  }
}
.contacts__phone {
  white-space: nowrap;
  text-align: right;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 22px;
  line-height: 136%;
  transition: color 0.3s;
}
.contacts__phone:hover {
  color: var(--theme-color-accent);
}
.contacts__link {
  margin: 2px 0px 0px 0px;
  text-align: right;
  font-family: var(--second-family);
  font-weight: 700;
  font-size: 16px;
  line-height: 150%;
  text-decoration: underline;
  text-decoration-skip-ink: none;
  text-align: right;
  color: var(--theme-color-accent);
  transition: opacity 0.3s;
}
.contacts__link:hover {
  opacity: 0.7;
}
@media (max-width: 992px) {
  .contacts__link {
    display: none;
  }
}

.dropdowm-header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  box-shadow: 0 30px 80px 0 rgba(43, 42, 41, 0.05);
  background: #fff;
  z-index: 10;
  border-radius: 40px;
  padding: 110px 25px 35px 25px;
  opacity: 0;
  max-height: 545px;
  pointer-events: none;
  transition: opacity 0.3s;
  overflow: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.dropdowm-header::-webkit-scrollbar {
  display: none;
}
.services-open .dropdowm-header {
  opacity: 1;
  pointer-events: auto;
}
@media (max-width: 1330px) {
  .dropdowm-header {
    padding: 110px 10px 35px 10px;
  }
}
@media (max-width: 992px) {
  .dropdowm-header {
    padding: 60px 10px 20px 10px;
    max-height: 490px;
    height: calc(100vh - 150px);
  }
}
.dropdowm-header__body ul {
  display: flex;
  flex-direction: column;
}
.dropdowm-header__body ul li {
  padding: 10px 0px;
  border-bottom: 1px dashed rgba(34, 57, 65, 0.1);
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
}
.dropdowm-header__body ul li img {
  flex: 0 0 50px;
  height: 50px;
  width: 50px;
  border-radius: 50%;
}
.dropdowm-header__body ul li a {
  display: flex;
  align-items: center;
  gap: 15px;
  transition: color 0.3s;
}
.dropdowm-header__body ul li a:hover {
  color: var(--theme-color-accent);
}
@media (max-width: 992px) {
  .dropdowm-header__body ul li a {
    padding: 0px 10px;
  }
}
.dropdowm-header__body ul li:first-child {
  padding: 0px 0px 10px 0px;
}
@media (max-width: 992px) {
  .dropdowm-header__body ul li:first-child {
    padding: 10px 0px;
  }
}
.dropdowm-header__body ul li:last-child {
  padding: 10px 0px 0px 0px;
  border-bottom: none;
}
@media (max-width: 1330px) {
  .dropdowm-header__body ul li {
    word-break: break-all;
  }
}
@media (max-width: 992px) {
  .dropdowm-header__body ul li {
    word-break: normal;
  }
}

body {
  color: var(--theme-color-text-common);
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
}
@media (max-width: 768px) {
  body {
    opacity: 0;
    transition: opacity 0.3s;
  }
}
.lock body {
  overflow: hidden;
}
.loaded body {
  opacity: 1;
}
.menu-open body::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 25;
  background-color: #f8fafb;
  background-image: -webkit-linear-gradient(top, #cfded6 0%, rgba(207, 222, 214, 0) 14.9%, rgba(207, 222, 214, 0) 100%);
  background-image: linear-gradient(to bottom, #cfded6 0%, rgba(207, 222, 214, 0) 14.9%, rgba(207, 222, 214, 0) 100%);
}
@media (max-width: 992px) {
  .services-open body {
    overflow: hidden;
  }
  .services-open body::before {
    content: "";
    cursor: pointer;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 40;
    background-color: rgba(34, 57, 65, 0.15);
  }
}

body.noscroll {
  overflow: hidden;
}

input,
button,
textarea {
  font-family: inherit;
  font-weight: 400;
  font-size: 16px;
}

fieldset,
fieldset legend {
  margin: 0;
  border: none;
  padding: 0;
}

button {
  cursor: pointer;
}

a {
  outline: none;
  color: currentcolor;
}

* {
  -webkit-tap-highlight-color: transparent;
}

*::selection {
  background-color: rgba(81, 161, 120, 0.1607843137);
  color: #223941;
}

.btn,
img {
  user-select: none;
}

.title1 {
  font-weight: 400;
  font-size: 80px;
  line-height: 112%;
}
@media (max-width: 992px) {
  .title1 {
    text-align: center;
  }
}
@media (max-width: 768px) {
  .title1 {
    font-size: 44px;
    line-height: 105%;
  }
}

.title2 {
  font-weight: 400;
  font-size: 60px;
  line-height: 107%;
}
@media (max-width: 768px) {
  .title2 {
    font-size: 38px;
    line-height: 121%;
    text-align: center;
  }
}

.heading-complex {
  display: flex;
  justify-content: space-between;
  margin: 0px 0px 65px 0px;
  gap: 20px;
}
@media (max-width: 768px) {
  .heading-complex {
    margin: 0px 0px 50px 0px;
  }
}
.heading-complex .title1 {
  flex: 1 1 auto;
}
.heading-complex .title2 {
  flex: 1 1 auto;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-radius: 50px;
  background-color: #ff6137;
  font-weight: 800;
  font-size: 18px;
  line-height: 144%;
  text-transform: uppercase;
  min-height: 90px;
  padding: 5px 60px;
  color: #fff;
  gap: 15px;
  transition: opacity 0.3s;
}
.btn img {
  width: 24px;
  height: 24px;
}
.btn:hover {
  opacity: 0.7;
}
@media (max-width: 768px) {
  .btn {
    font-size: 16px;
    min-height: 80px;
  }
}
@media (max-width: 480px) {
  .btn {
    width: 100%;
    padding: 5px 10px;
    gap: 12px;
  }
}

.block1 {
  margin: 0px 0px 100px 0px;
}
@media (max-width: 992px) {
  .block1 {
    margin: 0px 0px 70px 0px;
  }
}
@media (max-width: 768px) {
  .block1 {
    margin: 0px 0px 50px 0px;
  }
}

.arrows {
  display: flex;
  align-items: center;
  gap: 25px;
}
@media (max-width: 768px) {
  .arrows {
    justify-content: center;
    margin: 40px 0px 0px 0px;
  }
}

.arrow-prev {
  position: relative;
  z-index: 10;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 110px;
  height: 50px;
  border-radius: 50px;
  transition: background-color 0.3s;
}
.arrow-prev img {
  position: relative;
  z-index: 5;
  width: 60px;
  height: 16px;
  transform: rotate(-180deg);
}
.arrow-prev::before {
  content: "";
  background: #f8fafb;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(0px, -50%);
  width: 60px;
  height: 60px;
  border-radius: 50%;
  transition: opacity 0.3s;
}
.arrow-prev:hover {
  background-color: var(--theme-color-accent);
}
.arrow-prev:hover img {
  filter: brightness(0) invert(1);
}
.arrow-prev:hover::before {
  content: "";
  opacity: 0;
}

.arrow-next {
  position: relative;
  z-index: 10;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 110px;
  height: 50px;
  border-radius: 50px;
  transition: background-color 0.3s;
}
.arrow-next img {
  position: relative;
  z-index: 5;
  width: 60px;
  height: 16px;
}
.arrow-next::before {
  content: "";
